// src/Components/Common/Seo/createMetadata.jsx
export default function createMetadata({
  title,
  description,
  keywords,
  imageUrl,
  url,
  baseUrl, // dynamically passed from generateMetadata()
  showRobots = true,
  type = "website",
  // optional SEO fields
  author,
  publishedTime,
  modifiedTime,
  section,
  tags,
  price,
  currency = "USD",
  brand,
  availability,
  category,
  socialLinks = [],
}) {
  // -------------------------------------------------------------------
  // 1) Base domain (comes from generateMetadata with auto host header)
  // -------------------------------------------------------------------
  const finalBaseUrl = baseUrl || "https://www.spurtcommerce.com/";

  // extract domain only â†’ myshop.com
  const domainName = finalBaseUrl.replace(/^https?:\/\//, "");

  // -------------------------------------------------------------------
  // 2) Title + description + keywords (SMART OPTION A FALLBACKS)
  // -------------------------------------------------------------------
  const metaTitle = title || `Welcome to ${domainName}`;
  const metaDescription =
    description || `Explore high quality products at ${domainName}.`;
  const metaKeywords =
    keywords ||
    `${domainName}, online store, shop, ecommerce, products, buy online`;

  // -------------------------------------------------------------------
  // 3) Image URL handling
  // -------------------------------------------------------------------
  let finalImageUrl = imageUrl;
  if (!finalImageUrl) {
    finalImageUrl = `${finalBaseUrl}/img/logo-spurt.svg`;
  }
  if (!finalImageUrl.startsWith("http")) {
    finalImageUrl = `${finalBaseUrl}${finalImageUrl}`;
  }

  // -------------------------------------------------------------------
  // 4) Page URL (canonical)
  // -------------------------------------------------------------------
  let finalUrl = url || finalBaseUrl;
  if (!finalUrl.startsWith("http")) {
    finalUrl = `${finalBaseUrl}${finalUrl}`;
  }

  // -------------------------------------------------------------------
  // 5) Metadata base URL object
  // -------------------------------------------------------------------
  let metadataBaseUrl;
  try {
    metadataBaseUrl = new URL(finalBaseUrl);
  } catch {
    metadataBaseUrl = new URL("https://www.spurtcommerce.com/");
  }

  // -------------------------------------------------------------------
  // 6) Parse keyword string into array
  // -------------------------------------------------------------------
  const keywordsArray = metaKeywords
    .split(",")
    .map((x) => x.trim())
    .filter(Boolean);

  // -------------------------------------------------------------------
  // 7) Open Graph full config
  // -------------------------------------------------------------------
  const openGraphConfig = {
    title: metaTitle,
    description: metaDescription,
    url: finalUrl,
    siteName: domainName,
    type: type,
    locale: "en_US",
    images: [
      {
        url: finalImageUrl,
        width: 1200,
        height: 630,
        alt: metaTitle,
      },
    ],
  };

  // Article SEO
  if (type === "article") {
    openGraphConfig.publishedTime = publishedTime ?? undefined;
    openGraphConfig.modifiedTime = modifiedTime ?? undefined;
    if (author) {
      openGraphConfig.authors = Array.isArray(author) ? author : [author];
    }
    if (section) openGraphConfig.section = section;
    if (tags) openGraphConfig.tags = tags;
  }

  // -------------------------------------------------------------------
  // 8) Twitter Card SEO
  // -------------------------------------------------------------------
  const twitterConfig = {
    card: "summary_large_image",
    site: domainName,
    creator: domainName,
    title: metaTitle,
    description: metaDescription,
    images: [finalImageUrl],
  };

  // Product-specific
  if (type === "product" && price) {
    twitterConfig.card = "summary";
    twitterConfig.label1 = "Price";
    twitterConfig.data1 = `${currency} ${price}`;
  }

  // -------------------------------------------------------------------
  // 9) FINAL COMPLETE METADATA OBJECT
  // (Nothing removed. ALL SEO formats retained.)
  // -------------------------------------------------------------------
  return {
    metadataBase: metadataBaseUrl,

    title: metaTitle,
    description: metaDescription,
    keywords: keywordsArray,

    alternates: {
      canonical: finalUrl,
      languages: {
        "en-US": finalUrl,
        "x-default": finalUrl,
      },
    },

    openGraph: openGraphConfig,
    twitter: twitterConfig,

    appleWebApp: {
      capable: true,
      title: metaTitle,
      statusBarStyle: "default",
    },

    robots: showRobots
      ? {
          index: true,
          follow: true,
          googleBot: {
            index: true,
            follow: true,
            "max-image-preview": "large",
          },
        }
      : {
          index: false,
          follow: false,
          googleBot: {
            index: false,
            follow: false,
          },
        },

    other: {
      "theme-color": "#027600",
      "og:image:secure_url": finalImageUrl,
      "og:image:type": "image/jpeg",

      // Product SEO
      ...(type === "product" &&
        price && {
          "product:price:amount": String(price),
          "product:price:currency": currency,
          ...(availability && { "product:availability": availability }),
          ...(brand && { "product:brand": brand }),
          ...(category && { "product:category": category }),
        }),
      ...(socialLinks &&
        socialLinks.length > 0 &&
        socialLinks.reduce((acc, link, i) => {
          if (!acc["og:see_also"]) acc["og:see_also"] = [];
          acc["og:see_also"].push(link);
          return acc;
        }, {})),
    },
  };
}
