import axiosInstance from "../interceptor";

export const getcategory = async () => {
  const data = await axiosInstance.get(`list/category`);
  return data?.data;
};

//Seller list api
export const getsellerdata = async (offset) => {
  const data = await axiosInstance.get(
    `vendor-store/category-based-vendor-list?&categorySlug=&limit=10&offset=${offset}`,
  );
  return data?.data;
};

//Seller list count api
export const getsellercount = async () => {
  const data = await axiosInstance.get(
    `vendor-store/category-based-vendor-list?&categorySlug=&count=1`,
  );
  return data?.data;
};

export const getsubcategory = async (slug) => {
  const data = await axiosInstance.get(
    `list/specific-category?categorySlug=${slug}`,
  );
  return data?.data;
};

//country api
export const getProfilecountry = async () => {
  const data = await axiosInstance.get(
    `list/country-list?limit=0&offset=0&keyword=&count=0`,
  );
  return data?.data;
};

//state api
export const getProfilestate = async () => {
  const data = await axiosInstance.get(
    `list/zone?limit=0&offset=0&keyword=&count=0&countryId=0`,
  );
  return data?.data;
};

//banner api
export const getBannerList = async () => {
  const data = await axiosInstance.get(`list/banner`);
  return data?.data;
};

//footer api
export const getFooterList = async () => {
  const data = await axiosInstance.get(`pages`);
  return data?.data;
};

export const blogsseo = async (slug) => {
  const data = await axiosInstance.get(`/seo/blog/${slug}`);
  return data?.data;
};
